<template>
  <div class="category">
    <h1>Аксессуары</h1>
    <a-row gutter="16">
      <a-col v-for="item in accessories" :key="item.id" :xs="24" :sm="12" :md="8">
        <a-card :title="item.name" hoverable class="product-card">
          <img :src="item.image" alt="Accessory" style="width: 100%; height: 300px; object-fit: cover;" />
          <p>{{ item.description }}</p>
          <strong>{{ item.price }} ₽</strong>
          <a-button type="primary" block style="margin-top: 8px;" @click="addToCart(item)">
            В корзину
          </a-button>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  name: 'AccessoriesCategory',
  data() {
    return {
      accessories: [
        {
          id: 1,
          name: 'Чехол',
          description: 'Чехол накладка противоударный Gurdini Shockproof touch series для iPhone 14',
          price: 990,
          image: require('@/assets/chehol.jpg')
        },
        {
          id: 2,
          name: 'Адаптер питания Meta Quest',
          description: 'Адаптер питания Meta Quest 2 USB-C мощностью 18 Вт',
          price: 2990,
          image: require('@/assets/adapter.jpg')
        }
      ]
    }
  },
  methods: {
    addToCart(product) {
      // Получаем корзину из localStorage или создаём пустой массив
      let cart = JSON.parse(localStorage.getItem('cart')) || []

      // Проверяем, есть ли товар в корзине
      const exists = cart.find(item => item.id === product.id)

      if (!exists) {
        // Если товара нет в корзине, добавляем его
        cart.push(product)
        localStorage.setItem('cart', JSON.stringify(cart))  // Сохраняем корзину в localStorage
        this.$message.success(`Товар добавлен в корзину: ${product.name}`)
      } else {
        // Если товар уже есть в корзине
        this.$message.warning('Товар уже в корзине')
      }
    }
  }
}
</script>

<style scoped>
.category {
  padding: 40px;
}

.product-card {
  margin-bottom: 16px;
}
</style>
